* {
  box-sizing: border-box;
}

.container {
  max-width: 950px;
  margin: 0 auto;
  padding: 0 15px;
}

body {
  background-color: #896c9b;
}

.wrapper_info {
  text-align: center;
  color: #FFFFFF;
  margin: 80px 0;
}

.wrapper__info-title {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 60px;
  line-height: 100.69%;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}

.wrapper__info-subtitle {
  font-family: Roboto;
  font-weight: 500;
  font-size: 45px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #FFFFFF;
}

.wrapper__box {
  max-width: 866px;
  height: 510px;
  background: linear-gradient(108.92deg, #D774E8 0%, #8576A5 99.7%);
  box-shadow: 0px 0px 34px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  padding: 0 70px;
}

.wrapper__box-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.wrapper__box__title {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  letter-spacing: 0.015em;
  color: #FFFFFF;
}

.wrapper__items {
  max-width: 192px;
}

.wrapper__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 24px 0;
}

.wrapper__item-title {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  color: #E6A0FF;
}

.wrapper__item__input {
  width: 150px;
  height: 35px;
  background: #FFFFFF;
  border-radius: 5px;
  border: none;
  padding-left: 10px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  letter-spacing: 0.04em;
  color: #6C367E;
}

.wrapper__textarea {
  width: 322px;
  height: 95px;
  background: #FFFFFF;
  border-radius: 12px;
  margin-bottom: 65px;
}

.wrapper__btn {
  width: 220px;
  height: 50px;
  background: #AE79C8;
  box-shadow: 0px 4px 13px rgba(0, 0, 0, 0.29);
  border-radius: 65px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.wrapper__btn:hover {
  cursor: pointer;
}

.wrapper__btn-title {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 19px;
  text-align: center;
  letter-spacing: 0.065em;
  text-transform: uppercase;
  color: #FFFFFF;
}

.wrapper__function,
.wrapper__result {
  color: #fff;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 19px;
  text-transform: uppercase;
  margin: 10px 0;
}