* {
    box-sizing: border-box;
}
.container {
    max-width: 950px;
    margin: 0 auto;
    padding: 0 15px;
}
body {
    background-color: #896c9b;

}
.wrapper {
    &_info {
        text-align: center;
        color: #FFFFFF;
        margin: 80px 0;
    }
    &__info-title {
        font-family: Roboto;
        font-style: normal;
        font-weight: 500;
        font-size: 60px;
        line-height: 100.69%;
        letter-spacing: 0.04em;
        text-transform: uppercase;
    }
    &__info-subtitle {
        font-family: Roboto;
        font-weight: 500;
        font-size: 45px;
        text-align: center;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: #FFFFFF;
    }
    &__box {
        max-width: 866px;
        height: 510px;
        background: linear-gradient(108.92deg, #D774E8 0%, #8576A5 99.7%);
        box-shadow: 0px 0px 34px rgba(0, 0, 0, 0.3);
        border-radius: 10px;
        display: flex;
        justify-content: space-between;
        padding: 0 70px;
    }
    &__box-item {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    &__box__title {
        font-family: Roboto;
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        letter-spacing: 0.015em;
        color: #FFFFFF;
    }
    &__items {
        max-width: 192px;
    }
    &__item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 24px 0;
    }
    &__item-title {
        font-family: Roboto;
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        color: #E6A0FF;
    }
    &__item__input {
        width: 150px;
        height: 35px;
        background: #FFFFFF;
        border-radius: 5px;
        border: none;
        padding-left: 10px;
        font-family: Roboto;
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        letter-spacing: 0.04em;
        color: #6C367E;
    }
    &__textarea {
        width: 322px;
        height: 95px;
        background: #FFFFFF;
        border-radius: 12px;
        margin-bottom: 65px;
    }
    &__btn {
        width: 220px;
        height: 50px;
        background: #AE79C8;
        box-shadow: 0px 4px 13px rgba(0, 0, 0, 0.29);
        border-radius: 65px;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: center;
        &:hover {
            cursor: pointer;
        }
        margin-top: 20px;
    }
    &__btn-title {
        font-family: Roboto;
        font-style: normal;
        font-weight: bold;
        font-size: 19px;
        text-align: center;
        letter-spacing: 0.065em;
        text-transform: uppercase;
        color: #FFFFFF;
    }
    &__function,
    &__result {
        color: #fff;
        font-family: Roboto;
        font-style: normal;
        font-weight: bold;
        font-size: 19px;
        text-transform: uppercase;
        margin: 10px 0;
    }  
}